import { Component, OnInit } from '@angular/core';
import { PxtDialogFilterComponent } from '../../../pxt-dialog/pxt-dialog-filter/pxt-dialog-filter.component';
import { PxtFieldConfig } from '../../../../interfaces/pxt-field-config';
import { FormGroup, FormBuilder } from '@angular/forms';
import { MatDialog } from '@angular/material';

@Component({
  selector: 'pxt-input-filter',
  templateUrl: './pxt-input-filter.component.html',
  styleUrls: ['./pxt-input-filter.component.scss']
})
export class PxtInputFilterComponent implements OnInit {
  isDisabled = true;
  auto: boolean;

  field: PxtFieldConfig;
  group: FormGroup;
  value: "";
  constructor(public dialog: MatDialog) { }

  ngOnInit() {

    this.value = this.field.value;
  }

  //Method resposible for open dialog filter
  openFilter() {
    console.log(this.field);
    if (this.field.filters != undefined) {
      this.auto = true;
    }
    let dialogRef = this.dialog.open(PxtDialogFilterComponent, {
      width: '600px',
      panelClass: 'pxt-dialog',
      data: { auto: this.auto, filters: this.field.filters, controller: this.field.className, titleDialog: "Selecione: ( " + this.field.className.name + " )" }
    });
    dialogRef.afterClosed().subscribe(result => {
      if (result !== undefined) {
        this.field.value = result.codigo;
      }
      console.log(this.group);
      console.log(result);
    });
  }
}
